import React from "react";
import { Button, Space, Tooltip, Typography } from "antd";
import { CustomColumns, License } from "../../types";
import { setLicenseName } from "../utils/utils";
import { Modal } from "components/ui/Modal";
import { useQuery } from "react-query3";

import dayjs from "dayjs";
import utc from "dayjs/plugin/utc";
import localizedFormat from "dayjs/plugin/localizedFormat";
import NewLicenseModal from "./NewLicenseModal";
import TrialModal from "./trial-modal";
import { BranchesOutlined } from "@ant-design/icons";

const Table = React.lazy(() => import("components/ui/Table"));
const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));

dayjs.extend(utc);
dayjs.extend(localizedFormat);

const Licenses = () => {
  const { data: licenses, isLoading, refetch } = useQuery<License[]>(
    "/license",
    {
      enabled: false,
      refetchOnWindowFocus: false,
    }
  );

  const onDownloadDevLicense = (id) => {
    window.location.href = `/api/v1/license/developer/${id}`
  }

  const columns: CustomColumns<License>[] = [
    {
      title: "Product",
      dataIndex: "product",
      key: "product",
      editable: false,
      sorter: (a, b) => {
        return a.id - b.id;
      },
      render: (text, record) => {
        var productName = setLicenseName(text);
        return (
          <Space>
            {record?.developer && <Tooltip title="Developer license installed. All features are enabled but this instance can't be used remotely.">
              <BranchesOutlined />
            </Tooltip>}
            <Typography>{productName}</Typography>
          </Space>
        )
      }

    },
    {
      title: "Start Date",
      dataIndex: "startDate",
      key: "startDate",
      editable: false,
      sorter: (a, b) => {
        return a.id - b.id;
      },
      render: (text) => {
        return dayjs(text).format("lll");
      },
    },
    {
      title: "End Date",
      dataIndex: "endDate",
      key: "endDate",
      sorter: (a, b) => {
        return a.id - b.id;
      },
      render: (text, record) => {
        return record.status || dayjs(text).format("lll");
      },
    },
    {
      title: "Licensee",
      dataIndex: "licensee",
      key: "licensee",
      sorter: (a, b) => {
        return a.id - b.id;
      },
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      editable: false,
      render: (text, record) => {
        return (
          <Space>
            {!record?.developer && <Tooltip title="Developer licenses can be used for local development of PowerShell Universal. They do not allow for remote access.">
              <Button onClick={() => onDownloadDevLicense(record.id)} icon={<BranchesOutlined />}>Generate Developer License</Button>
            </Tooltip>}
            <DeleteButton resource={record} allowedWithOneWayGitSync={true} />
          </Space>
        );
      },
    },
  ];

  return (
    <Table
      title="Licenses"
      actions={[
        <Modal>
          <NewLicenseModal />
        </Modal>,
        licenses?.length > 0 ? <React.Fragment /> : <Modal><TrialModal /></Modal>
      ]}
      data={licenses}
      columns={columns}
      loading={isLoading}
      queryFn={refetch}
    />
  );
};

export default Licenses;
