import React, { Fragment } from "react";
import { ShoppingCartOutlined } from "@ant-design/icons/es/icons";
import { AccessControlTypes } from "../../types";
import { ModalContentBase, ModalOpenButton, useModalContext } from "components/ui/Modal";
import { Button, Typography } from "antd";
import { InfoCircleFilled } from "@ant-design/icons";

function TrialModal() {
  const { setIsOpen } = useModalContext()

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.View} allowedWithOneWayGitSync={true}>
        <Button type="primary" danger icon={<InfoCircleFilled />}>
          Free Version Info
        </Button>
      </ModalOpenButton>
      <ModalContentBase
        title="Free"
        onCancel={() => setIsOpen(false)}
        destroyOnClose={true}
        footer={[
          <Button
            href="https://ironmansoftware.com/pricing/powershell-universal"
            target="_blank"
            type="primary"
            icon={<ShoppingCartOutlined />}
          >
            Buy
          </Button>
        ]}
      >
        <Typography.Title level={4}>
          You're currently running the free version of PowerShell Universal
        </Typography.Title>

        <React.Fragment>
          <Typography style={{ fontWeight: "bold" }}>Universal API</Typography>
          <ul>
            <li>No RegEx Support</li>
            <li>No Authentication</li>
            <li>No Rate Limiting</li>
            <li>Limit of 25 APIs</li>
          </ul>
          <Typography style={{ fontWeight: "bold" }}>
            Universal Automation
          </Typography>
          <ul>
            <li>25 Jobs Per Day</li>
            <li>2 Concurrent Jobs</li>
            <li>No triggers</li>
          </ul>
          <Typography style={{ fontWeight: "bold" }}>
            Universal Dashboard
          </Typography>
          <ul>
            <li>Limited Options</li>
            <li>No Authentication</li>
          </ul>
          <Typography style={{ fontWeight: "bold" }}>
            Platform
          </Typography>
          <ul>
            <li>No Custom Login Page</li>
            <li>No Git Integration</li>
          </ul>
        </React.Fragment>


      </ModalContentBase>
    </Fragment>
  );
}

export default TrialModal;
