import React, { Fragment } from "react";
import { AccessControlTypes, License } from "../../types";
import { Button } from "antd";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Form, FormContentBase } from "components/ui/Form/Form";

export default function NewLicenseModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/license", "create", {} as License);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={true}>
        <Button children="Add New License" />
      </ModalOpenButton>
      <ModalContent title="New license" onClickOk={handleOk} formName="create_license_form">
        <Form schemaName="license">
          <FormContentBase
            name="create_license_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
